import { AbstractControl } from "@angular/forms";

export function produtoPerecivelValidator(control: AbstractControl) {

    const produtoPerecivel = control.get('ProdutoPerecivel')?.value as boolean ;
    const dataValidadade = control.get('DataValidade')?.value as Date ;
    if( produtoPerecivel && dataValidadade.toString().length == 0 ) {
        return { produtoPerecivel: true };
    } else
        return null;
    
}