import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ProdutoService } from '../produto.service';

declare var $: any;
import * as M from "materialize-css/dist/js/materialize";
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';



@Component({
  selector: 'app-listagem',
  templateUrl: './listagem.component.html',
  styleUrls: ['./listagem.component.css']
})
export class ListagemComponent implements OnInit {

    public listaProduto=[];
    constructor(private produtoService : ProdutoService,private route: Router) { }
    idproduto
    dtOptions: DataTables.Settings = {};

    ngOnInit(): void {
        this.obterListaProduto();
        this.dtOptions = {
            pagingType: 'full_numbers',
            pageLength: 10,
            language:{search:"Pesquisar",
                paginate: {next:"Próximo",first:"Primeiro",last:"Último",previous:"Anterior"} ,
                info:"Monstrando _START_ à _END_ de _TOTAL_ produtos",
                lengthMenu:"",
                zeroRecords:"Tabela vazia",
                infoEmpty: "",
                infoFiltered:   "",
            }
        };
        var elems = document.querySelectorAll('.modal');
        M.Modal.init(elems);
    }

    obterListaProduto(){
        this.produtoService.obterListaProduto().then((lista:Array<any>)=>{
            this.listaProduto = lista;
        })
    }

    apagarProduto(){
    this.produtoService.apagarProdutoPorId(this.idproduto)
    .then(mensagem=>{
        this.obterListaProduto();
        this.closeModal();
    })
    .catch(mensagem=>{
        this.closeModal();
    })
    }


    mostrarModal(id){
        this.idproduto = id
        const elem = document.getElementById('modal1');
        const instance = M.Modal.getInstance(elem);
        instance.open();
    }

    closeModal(){
        const elem = document.getElementById('modal1');
        const instance = M.Modal.getInstance(elem);
        this.dtOptions.destroy= true;
        instance.close();
        window.location.reload(); 
    }
}
