import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Validators } from '@angular/forms';
import {  CurrencyPipe} from '@angular/common';


import * as M from "materialize-css/dist/js/materialize";
import { dataValidadeValidator } from '../ValidacoesCustomizadas/dataValidadeValidator';
import { dataFabricacaoValidator } from '../ValidacoesCustomizadas/dataFabricacaoValidator';
import { produtoPerecivelValidator } from '../ValidacoesCustomizadas/ProdutoPerecivelValidator';
import { UnidadeValidator } from '../ValidacoesCustomizadas/UnidadeValidator';
import { ProdutoService } from '../produto.service';
import { ActivatedRoute } from '@angular/router';

declare var $: any;
@Component({
  selector: 'app-cadastro',
  templateUrl: './cadastro.component.html',
  styleUrls: ['./cadastro.component.css']
})
export class CadastroComponent implements OnInit {

 
  
	mask= '0*,000';
  	formulario: FormGroup;
  	idProduto;
 
  	constructor(private formBuilder: FormBuilder,private produtoService : ProdutoService, private route: ActivatedRoute) { }

  		ngOnInit(): void {

			this.idProduto = Number(this.route.snapshot.paramMap.get('id'));

		
			if(this.idProduto != 0)
				this.produtoService.obterProdutoPorId(this.idProduto-1).then(x=>{
					this.formulario.setValue(x);
				}).catch(x=>{})




			this.formulario = this.formBuilder.group({
				nome: ['',[Validators.required,Validators.maxLength(50)]],
				unidadeMedida: ['',[Validators.required]],
				quantidade: [],
				preco: ['',[Validators.required]],
				ProdutoPerecivel: ['',[]],
				DataValidade:['',[dataValidadeValidator]],
				DataFabricacao:['',[Validators.required]]
			},
			{
				validators:[produtoPerecivelValidator,dataFabricacaoValidator,UnidadeValidator]
			}
    	);
			$('input#input_text').characterCounter();
			M.updateTextFields();
			

      		var elems = document.querySelectorAll('select');
    		M.FormSelect.init(elems);

  }

 	alterarMascaraUnidadeMedida(){
		let unidadeMedida = this.formulario.get('unidadeMedida')?.value as string
		if(unidadeMedida == "Litro" || unidadeMedida == "Quilograma")
		{
			this.mask= '0*,000';
		}else{
			this.mask = '0*';
		}
	}

	cadastrarProduto(){
		let resource = JSON.stringify(this.formulario.value);
		if(this.idProduto == 0)
			this.produtoService.cadastrarProduto(resource);
		else
			this.produtoService.updateProduto(resource,this.idProduto-1);

		this.formulario.reset();
		
	}

}
