import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ProdutoService {

  constructor() { }


    cadastrarProduto(produto:string){
        let listaProdutos = JSON.parse(localStorage.getItem("listaProduto"))||[];
        listaProdutos.push(JSON.parse(produto));
        localStorage.setItem("listaProduto", JSON.stringify(listaProdutos));
    }

    updateProduto(produto:string, id){
        let listaProdutos = JSON.parse(localStorage.getItem("listaProduto"))||[];
        listaProdutos[id]= JSON.parse(produto);
        localStorage.setItem("listaProduto", JSON.stringify( listaProdutos));
    }

    obterProdutoPorId(id:number){
        return new Promise((resolve,reject)=>{
            let listaProdutos:Array<any> = JSON.parse(localStorage.getItem("listaProduto"))||[];
            if(listaProdutos.length > id )     
                resolve(listaProdutos[id]);
            reject([]);

        })    
    }

    obterListaProduto(){
        return new Promise((resolve,reject)=>{
            let listaProdutos= [];
             listaProdutos = JSON.parse(localStorage.getItem("listaProduto"));
                
            resolve(listaProdutos);

        })    
    }

    apagarProdutoPorId(id){
        return new Promise((resolve, reject)=>{
            if(id>=0){
                let listaProdutos:Array<any> = JSON.parse(localStorage.getItem("listaProduto"))||[];
                listaProdutos.splice(id,1);
                localStorage.setItem("listaProduto", JSON.stringify( listaProdutos));
                    resolve("Produto foi excluido com sucesso!");
            }
            else
                reject("Ocorreu um erro ao excluir produto");
        })
    }

}
