import { AbstractControl } from "@angular/forms";

export function dataValidadeValidator(control: AbstractControl) {
    if(control.value===null)
        return null;
    let dataValidade = new Date(control.value as Date);
    dataValidade.setDate(dataValidade.getDate() +1);
    if(dataValidade < new Date()){
        return { dataValidade: true };
    }
    else
        return null;
}