import { AbstractControl } from "@angular/forms";

export function UnidadeValidator(control: AbstractControl) {

    const unidadeMedida = control.get('unidadeMedida')?.value as string ;
    const quantidade = control.get('quantidade')?.value as string ;
    if( unidadeMedida?.length == 0 && quantidade?.length > 0) {
        return { unidadevalidator: true };
    } else
        return null;
    
}